@font-face {
  font-family: 'Minecraft';
  src: url("./resources/Minecraft.ttf") format("truetype");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input {
  font-family: "Minecraft";
  font-size: 20px;
}

input:focus {
  /* Remove the outline */
  outline: none;
}

#snakegame {
  display: flex;
  font-family: "Minecraft";
  color: whitesmoke;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
  background-color: #000e;
}

.infos {
  width: 20%;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  align-items: center;
}

.game {
  width: 60%;
  max-width: 800px;
  display: flex;
  justify-content: center;
}

.connectedServertext {
  font-size: 14px;
  color: #00ff00;
}

.score {
  font-size: 40px;
}

.peopleconnected {
  font-size: 30px;
  color : rgb(255,19,66)
}

.player {
  font-size: 20px;
}

.credits {
  font-size: 8px;
  color: #00ff00;
  justify-self: flex-end;
}

.help {
  font-size: 15px;
  color: #00ff00;
}